<template>
  <div class="pc_wrap">
      <!--  -->
      <el-row>
          <el-col :span="24" class="contnet_main_topic">
              <span></span>
          </el-col>
      </el-row>
      <div class="bos">
          {{ count }} +++ {{msgs}} === {{ content }}
      </div>
      <!--   -->
      <el-row>
          <el-col :span="24">
                <div class="title_btn">
                    <a @click="tabToggle(tab1)" :class="{active : tab1 == currentView}"></a>
                    <a @click="tabToggle(tab2)" :class="{active : tab2 == currentView}"></a>
                    <a @click="tabToggle(tab3)" :class="{active : tab3 == currentView}"></a>
                </div>
                <!--  -->
                <div class="tabs">
                    <component :is='currentView' keep-alive></component>
                </div>
          </el-col>
      </el-row>
      <!-- EventBus -->
      <el-row>
          <el-col :span="24" class="bus-wrap">
              <busSubA></busSubA>
              <busSubB></busSubB>
          </el-col>
      </el-row>
  </div>
</template>

<script>
import comA from './componentA.vue'
import comB from './componentB.vue'
import comC from './componentC.vue'
//EventBus 
import busSubA from './BusSubA.vue'
import busSubB from './BusSubB.vue'

//api
import { gettableDataApi } from '@/api/api'
//vuex
import{ mapState, mapGetters, mapMutations, mapActions } from 'vuex'
//mock api
import Mock from '../../mock'

export default {
  components:{
    comA,
    comB,
    comC,
    busSubA,
    busSubB
  },
  data() {
    return {
          tab1: 'comA', 
          tab2: 'comB', 
          tab3: 'comC', 
          currentView: 'comA' //
    }
  },
  created(){
      //localStorage
      if(localStorage.getItem('store')){
          this.$store.replaceState(Object.assign({},this.$store.state,JSON.parse(localStorage.getItem('store'))))
      }

      //vuexlocalStorage
      //or beforeunload
      window.addEventListener('beforeunload',() => {
          localStorage.setItem('store',JSON.stringify(this.$store.state))
      })

      //this.$store.replaceState()   store 
      //Object.assign(target, ...source)  source  target...source
  },
  mounted(){
      this.getTableData()
      this.getUser()
  },
  computed:{
    ...mapState('listData',['count','msgs']),
    ...mapGetters('homeData',['content','selectTab'])
  },
  methods: {
    ...mapActions('listData',['getListData','handleAdd']), //
    //
    //  ...mapActions({            
    //     getListData:"listData/getListData",            
    //     handleAdd:"listData/handleAdd"        
    // }),

    //json api
    getTableData(){
        gettableDataApi()
          .then(res => {
            this.tableList = res.data
          })
    },
    getUser(){ //mock
      this.$http.get('/api/paginationData')
        .then(res => {
            if(res){
                console.log(res.data)
            }
        }).catch(res => {
            alert(res)
        })
    },
    //
    tabToggle (tabText) { 
        this.currentView = tabText 
    } 
  }
};
</script>

<style scoped>
button{
    padding:10px;
    font-size:14px;
    color:#fff;
    background:black;
    border-style:none;
    cursor:pointer;
    margin:5px 0;
}

.title_btn{
  padding:0;
  font-size:14px;
}
.title_btn a{
  font-size:18px;
  color:#999;
  padding:20px;
  border:1px solid #d4d4d4;
  cursor: pointer;
}
.title_btn a:hover{
  color:#fff;
  background-color:#333;
}
/*  */
.title_btn a.active{ 
  color:#fff;
  background-color:#999;
}

.bus-wrap{
  display:flex;
  justify-content: space-between;
}
.bus-wrap .comA{
  flex:1 2 50%;
}

</style>