<template>
    <div class="comA">
        <!-- bus -->
            <p>
                > BusSubB Add number <br>
                > eventbus(payload) <br>
                > BusSubB eventbusnumbers
            </p>
            <div class="comA-num">
                <label for="number"></label>
                <input
                    type="text"
                    name="number"
                    placeholder=""
                    v-model="number"
                    class="busInput"
                />
                <button @click="addNum"></button>
            </div>
    </div>
</template>

<script>
//bus
import { bus } from '../../bus.js'

export default {
   data(){
       return{
            number:0
       }
   },
   mounted(){
       
   },
   methods:{
       //BusSubB Add number
        //eventbus(payload)
        //BusSubB eventbusnumbers
       addNum(){ //bus
           bus.$emit('addNum',parseInt(this.number))
       }
   }
}
</script>

<style>
/* ------------------------------
        goto top 
------------------------------ */
.busInput{
    padding:10px;
    margin-right:20px;
}
</style>