//getget
//@param {String} url [url]
//@param {Object} params []

//get
export function get(url, params){    
        return new Promise((resolve, reject) =>{        
            axios.get(url, {            
                params: params        
            }).then(res => {
                resolve(res);
            }).catch(err =>{
                reject(err)        
        })    
    });
}
//post
export function post(url, params) { //url--, params--
    return new Promise((resolve, reject) => {
         axios.post(url, QS.stringify(params))
        .then(res => { //
            resolve(res);
        })
        .catch(err =>{ //
            reject(err)
        })
    });
}
